<template>
  <div id="nervousbreakdown"></div>
</template>

<script>
export default {
  name: "NervousBreakdown",
  title: "神経衰弱",
  mounted() {
    this.initializeCards();
  },
  methods: {
    initializeCards() {
      for (var i = 1; i <= 26; i++) {
        for (var j = 1; j <= 2; j++) {
          this.cardObjects.push({
            kind: i
          });
        }
      }
      console.log(this.cardObjects.length());
    }
  },
  data: () => ({
    cardObjects: []
  })
};
</script>

